.tb_f_s_btn {
       display: flex;
       align-items: center;
       color: #fff;
       border-radius: 3px;
       position: fixed;
       font-size: 14px;
       top: 15px;
       right: 15px;
       z-index: 999999999;
       text-decoration: none;
       padding: 14px 16px;
       background: #f2545b;
       box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
       cursor: pointer;
}

.tb_f_s_btn .tb__icon {
       font-size: 18px;
       margin-right: 8px;
}